.odc-pipeline-run-logs {
  display: flex;
  flex: 1;
  width: 100%;
  padding: var(--pf-global--spacer--xl) 0;
}

.odc-pipeline-run-logs__nav {
  list-style-type: none;
}

.odc-pipeline-run-logs__nav .pf-c-nav__link {
  padding-left: 0px;
  padding-right: 0px;
}

.odc-pipeline-run-logs__nav .co-icon-and-text {
  position: relative;
  top: 0.35em;
}

.odc-pipeline-run-logs__namespan {
  margin-left: var(--pf-global--spacer--sm);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.odc-pipeline-run-logs__log {
  display: flex;
  align-items: center;
  font-size: 20px;
  height: calc(100% - var(--pf-global--spacer--lg));
  background: #999999;
  color: var(--pf-global--BackgroundColor--100);
}

.odc-pipeline-run-logs__tasklist {
  max-width: 30%;
  margin-top: var(--pf-global--spacer--lg);
  padding: 0px;
  padding-left: var(--pf-global--spacer--xl);
}

.odc-pipeline-run-logs__container {
  flex: 1;
  padding-left: var(--pf-global--spacer--xl);
  padding-right: var(--pf-global--spacer--xl);
}

.odc-pipeline-run-logs__container .co-m-pane__body {
  padding: 0px;
}

.odc-pipeline-run-logs__taskName {
  background-color: #555555;
  padding: 8px 16px;
  color: white;
}

.odc-pipeline-run-logs__message {
  margin-left: auto;
  margin-right: auto;
}

